 <template>
 	<section id="market">
 		<!-- <a class="cnyTitle">CNY/SGD <span class="title-right">SGD/USD</span></a> -->
 		<el-col :span="24" style="padding-bottom: 0px;padding-top:30px" class="toolbar">
        <el-form :inline="true" :model="formBuy" style="width:50%; margin:0 auto">
					<el-col :span = "24"  style="padding-bottom: 0px; " class="toolbar">
						<!-- <el-form-item :label="$t('purchase')">
							<el-input v-model="formBuy.buyCNY"  placeholder=""></el-input><span class="cny">CNY=1SGD</span>
						</el-form-item>
						<el-form-item :label="$t('purchase')">
							<el-input v-model="formBuy.buySGD" placeholder=""></el-input><span class="cny">SGD=1USD</span>
						</el-form-item> -->
						<el-form-item :label="$t('buySpread')">
							<el-input v-model="formBuy.buySpread" placeholder=""></el-input><span class="cny">XAu/USD</span>
						</el-form-item>
						<el-form-item :label="$t('purchase')">
							<el-input v-model="formBuy.buyPrice" placeholder="" disabled></el-input><span class="cny">XAu(g)/USD</span>
						</el-form-item>
					</el-col>
	        <el-col :span = "24" :offset="6" style="padding-bottom: 0px" class="toolbar">
	          <!-- <el-form-item :label="$t('sellingPrice')">
          		<el-input v-model="formBuy.sellCNY"  placeholder=""></el-input><span class="cny">CNY=1SGD</span>
	  	      </el-form-item>
        	  <el-form-item :label="$t('sellingPrice')">
					    <el-input v-model="formBuy.sellSGD" placeholder=""></el-input><span class="cny">SGD=1USD</span>
					  </el-form-item> -->
					  <el-form-item :label="$t('sellingSpread')">
					    <el-input v-model="formBuy.sellSpread" placeholder=""></el-input><span class="cny">XAu/USD</span>
					  </el-form-item>
					  <el-form-item :label="$t('sellingPrice')">
					    <el-input v-model="formBuy.sellPrice" placeholder="" disabled></el-input><span class="cny">XAu(g)/USD</span>
					  </el-form-item>
					  <el-form-item>
	 				    <el-button type="warning" @click="updatePrice">{{$t('confirm')}}</el-button>
 						</el-form-item>
					</el-col>
        </el-form>
    </el-col> 
		<el-table :data="onlyOne"   v-loading="userLoading" style="width: 100%;" :row-class-name="tableRowClassName" >
      <el-table-column prop="id"  label="">
      </el-table-column>
      <el-table-column prop="buyCnySgd" :label="$t('CurrentPurchasePrice')">
      </el-table-column>
      <el-table-column prop="spread"  :label="$t('CalculateThePurchasePrice')">
      </el-table-column>
      <el-table-column prop="sellCnySgd"  :label="$t('CurrentOffer')">
      </el-table-column>
      <el-table-column prop="utime" :label="$t('CalculateAndSell')">
      </el-table-column>
		</el-table>
		<h3>{{$t('historicalQuote')}}</h3>
		<el-table :data="historicalQuote"   v-loading="userLoading" style="width: 100%;" :row-class-name="tableRowClassName" >
      <el-table-column prop="id"  :label="$t('serial')">
      </el-table-column>
      <el-table-column prop="buyBtyUsdg" :label="$t('bidPrice')">
      </el-table-column>
      <el-table-column prop="sellBtyUsdg"  :label="$t('sellingRate')">
      </el-table-column>
      <el-table-column prop="buySpread"  :label="$t('buySpreadXau')">
      </el-table-column>
      <el-table-column prop="sellSpread"  :label="$t('sellingSpreadXau')">
      </el-table-column>
      <el-table-column prop="utime" :label="$t('updateTime')">
      </el-table-column>
    </el-table>
      <div class="page" v-show="total >10">
        <el-col :span="24" class="toolbar" >
           <el-pagination background layout="prev, pager, next" :total="total" @current-change="handleCurrentChange">
           </el-pagination>
        </el-col>
      </div>
 	</section>	
</template>
<script>
import Vue from 'vue'
import {ajax,dateStr2GmtTimeStamp,getDataYear,dateStrSecond} from '../../common/js/common.js'
import VueSocketio from 'vue-socket.io'
import socketio from 'socket.io-client'
import store from '../../store'
// Vue.use(VueSocketio,socketio('192.168.22.173:9091'));
Vue.use(VueSocketio,socketio('http://122.224.124.250:10011'));
export default {
	data(){
		return {
			userLoading:false,
			formBuy:{
				buyCNY:"",
				sellCNY:"",
				buySGD:"",
				sellSGD:"",
				spread:"",
				sellSpread:"",
				tradeFee:"",
				buyPrice:"",
				sellPrice:"",
			},
			liuDi:{
				buyCNY:"",
				sellCNY:"",
				buySGD:"",
				sellSGD:"",
				spread:"",
			},
			historicalQuote:[],
			total:0,
			onlyOne:[
				{
				id	:"0",
				buyCnySgd:"",
				sellCnySgd:"",
				spread:"",
				utime:"",
				originBuy:"",
				originSell:"",
				},
			],
			historyPrice:{
				page:"1",
	         	rowNum:10
	         },
		}
	},
	sockets:{
		connect(){
			console.log('socket connected')
			
		},
		ticker1m:function(res){
			let arr =JSON.parse(res)
			// console.log('haha')
			// console.log(this)
			// console.log(arr.data)
			// console.log(arr.data.market)
			// console.log(arr.data.market.buy)
			// console.log(arr.data.market.sell)
			this.onlyOne[0].buyCnySgd=arr.data.market.buy.toFixed(8);
			this.onlyOne[0].sellCnySgd =arr.data.market.sell.toFixed(8);
			this.onlyOne[0].originBuy = arr.data.originBuy.toFixed(8);
			this.formBuy.buyPrice = arr.data.originBuy.toFixed(5)
			this.onlyOne[0].originSell = arr.data.originSell.toFixed(8);
			this.formBuy.sellPrice = arr.data.originSell.toFixed(5)
				// console.log(this.liuDi)

			/*let buyXs= ((this.formBuy.buyCNY)/(this.liuDi.buyCNY))*((this.formBuy.buySGD)/(this.liuDi.buySGD))*((41-this.formBuy.spread)/(41-this.liuDi.spread));
			let	sellXs = ((this.formBuy.sellCNY)/(this.liuDi.sellCNY))*((this.formBuy.sellSGD)/(this.liuDi.sellSGD))*((41+parseInt(this.formBuy.spread))/(41+parseInt(this.liuDi.spread)));*/

			// let buyXs= ( (this.liuDi.sellCNY)/(this.formBuy.sellCNY) )*( (this.liuDi.sellSGD)/(this.formBuy.sellSGD) )*( (parseInt(this.onlyOne[0].originBuy)+parseInt((this.liuDi.buySpread)) )/(parseInt(this.onlyOne[0].originBuy)+parseInt(this.formBuy.buySpread)));
			// let	sellXs = ((this.liuDi.buyCNY)/(this.formBuy.buyCNY))*((this.liuDi.buySGD)/(this.formBuy.buySGD))*((parseInt(this.onlyOne[0].originSell)+parseInt(this.liuDi.sellSpread))/(parseInt(this.onlyOne[0].originSell)+parseInt(this.formBuy.sellSpread)));

			this.onlyOne[0].spread =parseFloat(this.onlyOne[0].buyCnySgd) * parseFloat(this.formBuy.buyPrice) / (parseFloat(this.formBuy.buyPrice) + parseFloat(this.formBuy.buySpread-this.liuDi.buySpread));
			this.onlyOne[0].spread = this.onlyOne[0].spread.toFixed(8);
			this.onlyOne[0].utime = parseFloat(this.onlyOne[0].sellCnySgd) * parseFloat(this.formBuy.sellPrice) / (parseFloat(this.formBuy.sellPrice) + parseFloat(this.formBuy.sellSpread-this.liuDi.sellSpread));
			this.onlyOne[0].utime = this.onlyOne[0].utime.toFixed(8);
			/* this.onlyOne[0].spread = buyXs*this.onlyOne[0].buyCnySgd;
			this.onlyOne[0].utime = sellXs*this.onlyOne[0].sellCnySgd; */

		},
		// xauUSDFP:function(result){
		// 	console.log("黄金:")
		// 	console.log(result)
		// },

	},		
	methods:{
		getSocket:function(){
				ajax(this,"http://122.224.124.250:10011/marhttp/ticker/ticker5m",{type:1,size:1},(res) =>{
					if(res.code==200){
						this.onlyOne[0].buyCnySgd=res.data.market.buy.toFixed(8);
						this.onlyOne[0].sellCnySgd =res.data.market.sell.toFixed(8);
						this.onlyOne[0].originBuy = res.data.originBuy.toFixed(8);
						this.onlyOne[0].originSell = res.data.originSell.toFixed(8);
						this.onlyOne[0].Spread = this.onlyOne[0].buyCnySgd
						this.onlyOne[0].utime = this.onlyOne[0].sellCnySgd
					}else{
						this.$notify({
							title:"提示",
							message:res.message,
							type:"warning"
						})
						return false;
					}
				},"POST")
		},
	 	tableRowClassName:function({row, rowIndex}){
	         if(rowIndex % 2 == 1){
	            return "success-row"
	         }
     	},
     	// 查询市价历史记录
        queryPriceHistory:function(){
	         // let data={
	         // 	page:"1",
	         // 	rowNum:10
	         // };
	      ajax(this,this.ApiConfig.marketPrice.queryPriceHistory,this.historyPrice,(res) => {
	        if(res.code ==200){
	          // console.log(res)
	          this.historicalQuote = res.data.rows;
	          let arr = this.historicalQuote;
	                arr.forEach((item,i,arr) => {
	                   arr[i].ctime =getDataYear('Y-M-D h:m:s',arr[i].ctime);
	                   arr[i].utime =getDataYear('Y-M-D h:m:s',arr[i].utime);
	                   arr[i].buyBtyUsdg = arr[i].buyBtyUsdg.toFixed(4);
	                   arr[i].sellBtyUsdg = arr[i].sellBtyUsdg.toFixed(4);
	                });
	          this.total = res.data.total;
	        }else{
	          this.$notify({
	            title:"提示",
	            message:res.message,
	            type:"warning"
	          })
	          return false;
	        }
	        
	      },"POST")
	    },
	    // 修改平台CNY/SGD汇率价格和点差值
	    updatePrice:function(){
	    	let data={
	    		buyCnySgd:this.formBuy.buyCNY,
	    		sellCnySgd:this.formBuy.sellCNY,
	    		buySpread:this.formBuy.buySpread,
	    		sellSpread:this.formBuy.sellSpread,
	    		tradeFee:this.formBuy.tradeFee,
	    		buySgdUsd:this.formBuy.buySGD,
	    		sellSgdUsd:this.formBuy.sellSGD,
	    		buyBtyUsdg:this.onlyOne[0].spread,
	    		sellBtyUsdg:this.onlyOne[0].utime,
	    	};
	    	// console.log("这个是改了的获取")
	    	// console.log(data)
    		 ajax(this,this.ApiConfig.marketPrice.updatePrice,data,(res) => {
	        if(res.code ==200){
	          // console.log(res)
	          // console.log("又一次改变留底")
	          	this.liuDi.buyCNY= data.buyCnySgd;
          		this.liuDi.sellCNY= data.sellCnySgd;
	          	this.liuDi.buySGD= data.buySgdUsd;
	          	this.liuDi.sellSGD=data.sellSgdUsd;
	          	this.liuDi.buySpread= data.buySpread;
	          	this.liuDi.sellSpread= data.sellSpread;
	          // console.log(this.liuDi)
	          this.queryPriceHistory();
	        }else{
	          this.$notify({
	            title:"提示",
	            message:res.message,
	            type:"warning"
	          })
	          return false;
	        }
	      },"POST")
	    },

	    handleCurrentChange:function(val){
       		this.historyPrice.page = val;
       		this.queryPriceHistory();
     },
     //查询当前平台市价(回显用)
     queryPrice:function(){
     	ajax(this,this.ApiConfig.marketPrice.queryPrice,{},(res) => {
	        if(res.code ==200){
	          console.log(res)
	          this.formBuy.buyCNY= res.data.buyCnySgd;
	          this.formBuy.sellCNY= res.data.sellCnySgd;
	          this.formBuy.buySGD= res.data.buySgdUsd;
	          this.formBuy.sellSGD= res.data.sellSgdUsd;
	          this.formBuy.buySpread= res.data.buySpread;
	          this.formBuy.sellSpread= res.data.sellSpread;
	          this.formBuy.tradeFee= res.data.tradeFee;
	          // 这个用来算系数
	          this.liuDi.buyCNY= res.data.buyCnySgd;
	          this.liuDi.sellCNY= res.data.sellCnySgd;
	          this.liuDi.buySGD= res.data.buySgdUsd;
	          this.liuDi.sellSGD= res.data.sellSgdUsd;
	          this.liuDi.buySpread= res.data.buySpread;
	          this.liuDi.sellSpread= res.data.sellSpread;
	          // console.log("确认留底")
	          // console.log(this.liuDi)
	        }else{
	        	console.log(res)
	          this.$notify({
	            title:"提示",
	            message:res.message,
	            type:"warning"
	          })
	          return false;
	        }
	      },"POST")
     },

	},
	mounted:function(){
		this.queryPriceHistory();
		this.queryPrice();
	},
	created(){
		this.getSocket();
	}
}
</script>

<style scoped postcss="lang">
.cnyTitle{
	font-weight: bold;

}
.title-right{
	margin-left: 225px;
}
.tradeFee-right{
	margin-left: 320px;
}
.cny{
	font-size: 14px;
	color: #a0a0a0;
	padding-left:8px;
	padding-right: 35px;
}

</style>

<style>
	#market{
		min-width: 1700px;
		overflow:scroll;
		.el-input{
			width: 100px;
		}
		.el-button{
			/* width: 120px;
			height: 40px; */
			background-color: #bf9764;
			/* font-size: 16px; 
			border-radius: 6px;
			border: none;
			outline: none; */
		}
		.el-form-item__label{
			font-size: 14px;
    		color: #000;
		}

		.el-input__inner{
			font-size: 14px;
			}
}
</style>
 


